import { Events, Gallery, Navigation, Page, Pages } from "../../services/models";
import { NavigationBar } from "../";

const navigation: ReadonlyArray<Navigation> = [
  {
    title: "Home",
    page: Pages.Homepage,
  },
  {
    title: "Events",
    page: Pages.Events,
    subPages: [
      { title: "Upcoming Events", page: Events.Upcoming }, { title: "Past Events", page: Events.Past }
    ]
  },
  {
    title: "Gallery",
    page: Pages.Gallery,
    subPages: [
      { title: "Photos", page: Gallery.Photos }, { title: "Videos", page: Gallery.Videos }
    ]
  },
  {
    title: "Suggestions",
    page: Pages.Suggestions
  },
  {
    title: "About Us",
    page: Pages.About,
  }
]

interface Props {
  readonly currentPage: Page;
  readonly setPage: (currentPage: Page) => void;
}

export const Header = ({ currentPage, setPage }: Props) =>
  <div style={styles.header}>
    <div style={styles.logoContainer}>
      <img
        onClick={() => setPage({page: Pages.Homepage})}
        src={`${process.env.PUBLIC_URL}/logo.jpeg`}
        style={styles.logo}
        alt={"logo"} />
      <img
        style={styles.banner}
        src={`${process.env.PUBLIC_URL}/shaniwar-wada.jpg`} />
    </div>
    <NavigationBar currentPage={currentPage} setPage={setPage} options={navigation} />
  </div>

const styles = {
  header: {
    padding: "0.5% 1%",
  },
  logoContainer: {
    display: "flex",
    justifyContent: "space-between"
  },
  logo: {
    height: "8%",
    width: "8%",
    cursor: "pointer"
  },
  banner: {
    height: "10%",
    width: "10%",
    alignSelf: "end"
  }
} as Record<string, React.CSSProperties>;